}

button,
input[type=submit] {
  display: inline-block;
  cursor: pointer;
  background: transparent none;
  box-shadow: none;
}

input,
textarea,
button,
select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

input:focus,
input:active,
button:focus,
button:active {
