}

::-webkit-scrollbar-thumb {
  border: 1px solid var(--border-color);
  border-radius: 2px;
  background-color: var(--themes-color);
  background-image: -webkit-linear-gradient(45deg, var(--background-thumb) 25%, transparent 25%, transparent 50%, var(--background-thumb) 50%, var(--background-thumb) 75%, transparent 75%, transparent);
}

::-webkit-scrollbar-thumb:hover {
  background-color: var(--background-thumb);
  background-image: -webkit-linear-gradient(45deg, var(--themes-color) 25%, transparent 25%, transparent 50%, var(--themes-color) 50%, var(--themes-color) 75%, transparent 75%, transparent);
}

@font-face {
  font-family: "icon-set";
  font-display: swap;
  src: url("../fonts/icon-set.eot") format("eot"), url("../fonts/icon-set.woff") format("woff"), url("../fonts/icon-set.woff2") format("woff2"), url("../fonts/icon-set.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
.perform {
  position: relative;
  z-index: 11;
}
.perform__body {
  position: relative;
  min-height: calc(100vh - 12rem);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 10rem;
  padding-bottom: 2rem;
}
.perform__title {
  position: relative;
  z-index: 9;
}
.perform__title h1 {
  font-weight: 700;
  font-size: clamp(3.4375rem, 2.427125rem + 4.051875vw, 8.489375rem);
  -webkit-text-stroke: 2px #7e6aa8;
  text-transform: uppercase;
  line-height: 100%;
  margin-bottom: 2rem;
}
.perform__title h1 span {
  display: block;
}
.perform__title h1 span:nth-child(1) {
  color: transparent;
  -webkit-text-stroke: 1px #48386b;
}
.perform__content {
  width: 100%;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  gap: 1.6rem;
}
@media (max-width: 768px) {
  .perform__content {
    flex-direction: column;
  }
}
@media (max-width: 768px) {
  .perform__content {
    height: 100%;
  }
}
.perform__column {
  flex: 0 1 50%;
}
.perform__column:nth-child(1) .perform__items {
  padding-top: 3.5rem;
}
@media (max-width: 620px) {
  .perform__column:nth-child(1) .perform__items {
    padding-top: 4rem;
  }
}
.perform__column:nth-child(2) {
  border-radius: 0.4rem;
  min-width: 260px;
  overflow: hidden;
}
.perform__column:nth-child(2) .perform__items {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (max-width: 768px) {
  .perform__column:nth-child(2) {
    padding-top: 0;
  }
}
.perform__text {
  position: relative;
  border-radius: 2rem;
}
.perform__shape-block {
  width: 100%;
  float: left;
  shape-outside: polygon(0px 28px, 0px 130%, 30% 130%);
  opacity: 0;
}
.perform__text-block {
  height: auto;
}
.perform__text-block p {
  line-height: 115%; /* 25/16 */
  filter: contrast(160%);
  transform: scaleY(1.2);
}
.perform__text-block p:not(:last-child) {
  margin-bottom: 0.5rem;
}
.perform__footer {
  position: absolute;
  bottom: 0;
  width: 100%;
}
.perform__footer .arrow {
  margin: 4rem auto -2rem;
  width: 3.8rem;
  height: 10rem;
}
.perform__footer .arrow span {
  display: block;
  width: 3rem;
  height: 3rem;
  border-bottom: 0.5rem solid #e887be;
  border-right: 0.5rem solid #48386b;
  transform: rotate(45deg);
  margin: -1.6rem 0;
  opacity: 0;
}
.perform__footer .arrow span:nth-child(2) {
  animation-delay: -0.2s;
}
.perform__footer .arrow span:nth-child(3) {
  animation-delay: -0.4s;
}
.perform__footer .arrow.anime span {
  opacity: 1;
  animation: arrow 2s infinite;
}

@keyframes arrow {
  0% {
    opacity: 0;
    transform: translate(0, -20px) rotate(45deg);
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: translate(0, 20px) rotate(45deg);
  }
}
