  align-items: center;
  justify-content: space-between;
}
.slide__pagination {
  width: auto;
  display: flex;
  align-items: center;
  gap: 2rem;
  padding: 3rem 0 0 2rem;
}
.slide__pagination .swiper-pagination-bullet {
  cursor: pointer;
  filter: contrast(190%);
  display: block;
  background-color: #48386b;
  opacity: 1;
  border-radius: 0.1rem;
  width: 1rem;
  height: 1rem;
  transition: transform 0.5s ease, background-color 0.5s ease, scale 0.5s ease;
}
.slide__pagination .swiper-pagination-bullet-active {
  background-color: #e887be;
  scale: 1.5;
