.slide__slide-button {
  position: relative;
  display: flex;
  align-items: center;
  padding: 3rem 2rem 0 0;
}
.slide__prev {
  cursor: pointer;
  width: auto;
  left: 0;
  top: auto;
  display: flex;
  align-items: center;
  filter: contrast(190%);
}
.slide__prev::before {
  content: "next";
  position: relative;
  font-family: swiper-icons;
  font-size: 1.6rem;
  font-weight: 900;
  color: #8a9064;
  top: 1px;
  display: flex;
  align-items: center;
  transform: rotate(-180deg);
  text-transform: none !important;
  letter-spacing: 0;
  font-variant: initial;
  line-height: 1;
}
.slide__next {
  cursor: pointer;
  width: auto;
  right: 0;
  top: auto;
  display: flex;
  align-items: center;
  filter: contrast(190%);
}
.slide__next::after {
  content: "next";
  position: relative;
  font-family: swiper-icons;
  font-size: 1.6rem;
  font-weight: 900;
  color: #48386b;
  display: flex;
  align-items: center;
  text-transform: none !important;
  letter-spacing: 0;
  font-variant: initial;
  line-height: 1;
}
.slide__count {
