  font-weight: 600;
  color: #48386b;
  width: 100%;
  white-space: nowrap;
}
.slide__line-prev {
  width: 36px;
  height: 2px;
  background-color: #48386b;
  margin-left: 0.5rem;
  margin-right: 1.6rem;
  filter: contrast(190%);
}
.slide__line-next {
  width: 36px;
  height: 2px;
  background-color: #48386b;
  margin-left: 1.6rem;
  margin-right: 0.5rem;
  filter: contrast(190%);
}